
import indexCss from "./index.module.scss";
import {baseURL  } from "../../utils/request";


// house  
export default ({house})=>{
  return <div  className={indexCss.house_item}  >
  <div className={indexCss.house_img_wrap}>
    <img src={baseURL + house.houseImg} alt="" />
  </div>
  <div className={indexCss.house_info_wrap}>
    <div className={indexCss.house_item_title}>{house.title}</div>
    <div className={indexCss.house_item_desc}>{house.desc}</div>
    <div className={indexCss.house_item_tags}>{house.tags}</div>
    <div className={indexCss.house_item_price}><span>{house.price} 元/月 </span></div>
  </div>
</div>
}